import React, { useState } from 'react';
import {
  Card,
  Tabs,
  Form,
  Input,
  Button,
  Select,
  Space,
  message,
  Spin,
  Typography,
  Divider,
  Row,
  Col,
} from 'antd';
import { RobotOutlined, FileTextOutlined, BugOutlined, ApiOutlined } from '@ant-design/icons';
import { analyzeRequirement, generateTestCases, generateAPITests, generateUITests } from '../services/aiService';

const { Title, Paragraph } = Typography;
const { TextArea } = Input;
const { Option } = Select;

const AIEngine: React.FC = () => {
  const [loading, setLoading] = useState(false);
  const [results, setResults] = useState<any>(null);

  const handleRequirementAnalysis = async (values: any) => {
    setLoading(true);
    try {
      const response = await analyzeRequirement(values);
      setResults({ type: 'requirement_analysis', data: response });
      message.success('');
    } catch (error) {
      message.error('');
    } finally {
      setLoading(false);
    }
  };

  const handleTestCaseGeneration = async (values: any) => {
    setLoading(true);
    try {
      const response = await generateTestCases(values);
      setResults({ type: 'test_case_generation', data: response });
      message.success('');
    } catch (error) {
      message.error('');
    } finally {
      setLoading(false);
    }
  };

  const handleAPITestGeneration = async (values: any) => {
    setLoading(true);
    try {
      const response = await generateAPITests(values);
      setResults({ type: 'api_test_generation', data: response });
      message.success('API');
    } catch (error) {
      message.error('API');
    } finally {
      setLoading(false);
    }
  };

  const handleUITestGeneration = async (values: any) => {
    setLoading(true);
    try {
      const response = await generateUITests(values);
      setResults({ type: 'ui_test_generation', data: response });
      message.success('UI');
    } catch (error) {
      message.error('UI');
    } finally {
      setLoading(false);
    }
  };

  const renderResults = () => {
    if (!results) return null;

    return (
      <Card title="AI" style={{ marginTop: 16 }}>
        <pre style={{ background: '#f5f5f5', padding: 16, borderRadius: 6, overflow: 'auto' }}>
          {JSON.stringify(results.data, null, 2)}
        </pre>
      </Card>
    );
  };

  const items = [
    {
      key: 'requirement-analysis',
      label: (
        <span>
          <FileTextOutlined />
          
        </span>
      ),
      children: (
        <Card>
          <Form layout="vertical" onFinish={handleRequirementAnalysis}>
            <Form.Item
              label=""
              name="requirement_text"
              rules={[{ required: true, message: '' }]}
            >
              <TextArea rows={6} placeholder="..." />
            </Form.Item>
            <Form.Item label="" name="project_context">
              <TextArea rows={3} placeholder="..." />
            </Form.Item>
            <Form.Item label="" name="test_focus">
              <Select
                mode="tags"
                placeholder=""
                options={[
                  { label: '', value: 'functional' },
                  { label: '', value: 'performance' },
                  { label: '', value: 'security' },
                  { label: '', value: 'compatibility' },
                  { label: '', value: 'ux' },
                ]}
              />
            </Form.Item>
            <Form.Item>
              <Button type="primary" htmlType="submit" loading={loading} icon={<RobotOutlined />}>
                
              </Button>
            </Form.Item>
          </Form>
        </Card>
      ),
    },
    {
      key: 'test-case-generation',
      label: (
        <span>
          <BugOutlined />
          
        </span>
      ),
      children: (
        <Card>
          <Form layout="vertical" onFinish={handleTestCaseGeneration}>
            <Form.Item
              label=""
              name="requirement_text"
              rules={[{ required: true, message: '' }]}
            >
              <TextArea rows={4} placeholder="..." />
            </Form.Item>
            <Form.Item
              label=""
              name="test_type"
              rules={[{ required: true, message: '' }]}
            >
              <Select placeholder="">
                <Option value="functional"></Option>
                <Option value="api"></Option>
                <Option value="ui">UI</Option>
              </Select>
            </Form.Item>
            <Form.Item label="" name="test_scope">
              <TextArea rows={3} placeholder="..." />
            </Form.Item>
            <Form.Item>
              <Button type="primary" htmlType="submit" loading={loading} icon={<RobotOutlined />}>
                
              </Button>
            </Form.Item>
          </Form>
        </Card>
      ),
    },
    {
      key: 'api-test-generation',
      label: (
        <span>
          <ApiOutlined />
          API
        </span>
      ),
      children: (
        <Card>
          <Form layout="vertical" onFinish={handleAPITestGeneration}>
            <Form.Item
              label="API"
              name="api_documentation"
              rules={[{ required: true, message: 'API' }]}
            >
              <TextArea rows={6} placeholder="API..." />
            </Form.Item>
            <Form.Item
              label="URL"
              name="base_url"
              rules={[{ required: true, message: 'URL' }]}
            >
              <Input placeholder="https://api.example.com" />
            </Form.Item>
            <Form.Item label="" name="test_scenarios">
              <Select
                mode="tags"
                placeholder=""
                options={[
                  { label: '', value: 'normal' },
                  { label: '', value: 'error' },
                  { label: '', value: 'boundary' },
                  { label: '', value: 'performance' },
                ]}
              />
            </Form.Item>
            <Form.Item>
              <Button type="primary" htmlType="submit" loading={loading} icon={<RobotOutlined />}>
                API
              </Button>
            </Form.Item>
          </Form>
        </Card>
      ),
    },
    {
      key: 'ui-test-generation',
      label: (
        <span>
          <BugOutlined />
          UI
        </span>
      ),
      children: (
        <Card>
          <Form layout="vertical" onFinish={handleUITestGeneration}>
            <Form.Item
              label="URL"
              name="page_url"
              rules={[{ required: true, message: 'URL' }]}
            >
              <Input placeholder="https://example.com/login" />
            </Form.Item>
            <Form.Item
              label=""
              name="user_actions"
              rules={[{ required: true, message: '' }]}
            >
              <Select
                mode="tags"
                placeholder=""
                options={[
                  { label: '', value: 'click_login' },
                  { label: '', value: 'input_username' },
                  { label: '', value: 'input_password' },
                  { label: '', value: 'submit_form' },
                ]}
              />
            </Form.Item>
            <Form.Item label="" name="test_scenarios">
              <Select
                mode="tags"
                placeholder=""
                options={[
                  { label: '', value: 'normal_login' },
                  { label: '', value: 'wrong_password' },
                  { label: '', value: 'empty_username' },
                  { label: '', value: 'remember_password' },
                ]}
              />
            </Form.Item>
            <Form.Item>
              <Button type="primary" htmlType="submit" loading={loading} icon={<RobotOutlined />}>
                UI
              </Button>
            </Form.Item>
          </Form>
        </Card>
      ),
    },
  ];

  return (
    <div>
      <Title level={2}>
        <RobotOutlined /> AI
      </Title>
      <Paragraph>
        AIAPIUI
      </Paragraph>
      
      <Divider />
      
      <Spin spinning={loading}>
        <Tabs items={items} />
        {renderResults()}
      </Spin>
    </div>
  );
};

export default AIEngine; 